.header {
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: var(--z-fixed);
  background-color: var(--body-color);
}
.nav {
  height: calc(var(--header-height) + 1.5rem);
  display: flex;
  justify-content: space-between;
  align-items: center;
  column-gap: 1rem;
}
.nav__logo,
.nav__toggle {
  color: var(--title-color);
  font-weight: var(--font-medium);
  
}
.nav__list {
  display: flex;
  column-gap: 2rem;
}
.nav__link {
  display: flex;
  flex-direction: column;
  align-items: center;
  font-size: var(--small-font-size);
  color: var(--title-color);
  font-weight: var(--font-medium);
  transition: .3s;
  -webkit-transition: .3s;
  -moz-transition: .3s;
  -ms-transition: .3s;
  -o-transition: .3s;
}
.nav__icon,
.nav__close,
.nav__toggle {
  display: none;
}

/* active link */
.active-link,
.nav__link:hover{
    color: var(--title-color-dark);
}

.scroll-header{
  box-shadow: 0 -1px 4px rgba(1, 1, 1, 0.349);
  border-bottom-left-radius: 1rem;
border-bottom-right-radius: 1rem;
}

/* For medium devices */
@media screen and (max-width: 768px) {
  .header {
    top: initial;
    bottom: 0;
    border-radius: 3rem;
    -webkit-border-radius: 3rem;
    -moz-border-radius: 3rem;
    -ms-border-radius: 3rem;
    -o-border-radius: 3rem;
}
  .nav{
    height: 3.5rem;
}
  .nav__menu{
    position: fixed;
    bottom: -100%;
    left: 0;
    width: 100%;
    background-color: var(--body-color);
    padding: 2rem 1.5rem 4rem ;
    box-shadow: 0 -1px 4px rgba(0,0,0,0.15);
    border-radius: 1.5rem 1.5rem 0 0;
    transition: .2s;
    -webkit-transition: .2s;
    -moz-transition: .2s;
    -ms-transition: .2s;
    -o-transition: .2s;
}
.show-menu{
  bottom: 0;
  border-radius: 2rem;
  -webkit-border-radius: 2rem;
  -moz-border-radius: 2rem;
  -ms-border-radius: 2rem;
  -o-border-radius: 2rem;
}
  .nav__list{
    display: grid;
    grid-template-columns: repeat(3,1fr);
    gap: 2rem;
  }
  .nav__icon{
    font-size: 1.2rem;
  }
  .nav__close{
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor: pointer;
    color: var(--title-color);
  }
  .nav__close:hover{
    color: var(--title-color-dark);
  }
  .nav__toggle{
    font-size: 1.1rem;
    cursor: pointer;
  }

  .nav__icon,
  .nav__close,
  .nav__toggle{
    display: block;
  }
}
@media screen and (max-width: 350px) {
  
    .nav__menu{
      padding: 2rem 0.25rem 4rem;
    }
    .nav__list{
      column-gap: 0;
    }

}